<template>
  <div>
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="描述">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="内容">
        <editor v-model="formData.content" @getText="getText"></editor>
      </el-form-item>
      <el-form-item>
        <el-button type="success" @click="submit">立即创建</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '@/components/editor/index'
  export default {
    name: "add",
    components: {
      editor
    },
    data(){
      return{
        formData:{
          title: '',
          content: '',
          contentText: ''
        }
      }
    },
    methods: {
      getText(val){
          this.formData.contentText = val

      },
      submit(){
        this.$axios.post('addDiscuss',this.formData).then(res=>{
          if(res.code==200){
            this.$message({
              type: 'success',
              message: '提交成功!'
            });
            setTimeout(()=>{
              this.$router.push("/crmdemocratic")
            },1000)
          }else{
            this.$message({
              type: 'warning',
              message: res.msg
            });
          }
        })
      }
    }
  }
</script>

<style scoped>

</style>



